.header {
	position: relative;
	&__top {
		padding: 51px 0 10px;
		border-bottom: 1px solid #d9d9d9;
		@include sm {
			padding: 12px 0 6px; } }
	&__nav {
		padding: 10px 0 0;
		border-bottom: 1px solid #d9d9d9;
		&.is-open {
			transform: translateX(-274px); }
		@include sm {
			position: absolute;
			top: 47px;
			left: 100%;
			padding: 0;
			width: 274px;
			transition: transform .3s;
			background-color: white;
			border: 1px solid #abafb5; }
		.header__inner {
			@include sm {
				padding: 0; } } }
	&__logo {
		float: left;
		margin: -30px 0 -7px;
		@include sm {
			margin: 0; } }
	&__info {
		float: right;
		color: #787a7f;
		font-size: 18px;
		@include font-smoothing;
		@include sm {
			display: none; } } }
.btn-menu {
	position: relative;
	float: right;
	margin-top: 2px;
	width: 16px;
	height: 16px;
	background-color: transparent;
	display: none;
	@include sm {
		display: block; }
	&.is-active {
		&:before {
			transform: translate(2px, 13px) rotate(-45deg); }
		&:after {
			transform: translate(2px, -13px) rotate(45deg); }
		span {
			opacity: 0; } }
	&:before,
	&:after,
	span {
		position: absolute;
		content: "";
		left: 0;
		width: 16px;
		height: 2px;
		transition: opacity .3s, transform .3s;
		border-radius: 2px;
		background-color: #9d9d9d; }
	&:before {
		top: 0;
		transform-origin: top left; }
	&:after {
		bottom: 0;
		transform-origin: bottom left; }
	span {
		top: 50%;
		margin-top: -1px;
		&:before {
			position: absolute;
			content: "";
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			width: 30px;
			height: 30px; } } }
.logo {
	font-size: 24px;
	display: inline-block;
	@include clr;
	img {
		float: left;
		@include sm {
			width: 26px; } }
	&__text {
		float: right;
		margin: 22px 0 0 3px;
		color: #181819;
		font-weight: 400;
		@include sm {
			margin: 12px 0 0 3px;
			font-size: 12px; } } }
.nav {
	@include justify;
	@include sm {
		text-align: initial;
		text-justify: initial;
		text-align-last: initial; }
	&__item {
		vertical-align: top;
		display: inline-block;
		@include sm {
			display: block; } }
	&__link {
		position: relative;
		padding: 18px 6px;
		color: #003d8c;
		line-height: 1.4;
		font-size: 22px;
		display: block;
		@include md {
			font-size: 16px; }
		@include sm {
			padding: 14px 23px;
			color: #090909;
			font-size: 15px; }
		&:before {
			position: absolute;
			content: "";
			left: 0;
			right: 0;
			bottom: 0;
			height: 5px;
			opacity: 0;
			transition: opacity .3s;
			background-color: #fcd845;
			@include sm {
				display: none; } }
		@include hover {
			&:before {
				opacity: 1; } }
		&.is-active {
			@include sm {
				background-color: #d0d8e4; }
			&:before {
				opacity: 1; } } } }
